/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import "../../node_modules/swiper/swiper-bundle.css";
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
  transition: all 0.3s linear;
}

html {
  font-family: "Roboto", sans-serif;
  height: 100%;
  font-size: 62.5%;
  overflow-x: hidden;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: #000;
}
@media (max-width: 108em) {
  html {
    font-size: 9px;
  }
}
@media (max-width: 105em) {
  html {
    font-size: 8.75px;
  }
}
@media (max-width: 91.5em) {
  html {
    font-size: 7.9px;
  }
}
@media (max-width: 91em) {
  html {
    font-size: 8.4px;
  }
}
@media (max-width: 90em) {
  html {
    font-size: 7.9px;
  }
}
@media (max-width: 80em) {
  html {
    font-size: 7.2px;
  }
}
@media (max-width: 72em) {
  html {
    font-size: 6.5px;
  }
}
@media (max-width: 64em) {
  html {
    font-size: 5.5px;
  }
}
@media (max-width: 48em) {
  html {
    font-size: 10px;
    -webkit-text-size-adjust: none;
  }
}
@media (max-width: 44em) {
  html {
    font-size: 9px;
  }
}
@media (max-width: 38em) {
  html {
    font-size: 8px;
  }
}
@media (max-width: 34em) {
  html {
    font-size: 7px;
  }
}
@media (max-width: 30em) {
  html {
    font-size: 6px;
  }
}
@media (max-width: 26em) {
  html {
    font-size: 4.5px;
  }
}
@media (max-width: 21em) {
  html {
    font-size: 4.4px;
  }
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #F6F8F9;
  color: #373535;
}

header {
  flex: 0 0 auto;
}

main {
  flex: 1 0 auto;
  padding-top: 7rem;
}

footer {
  flex: 0 0 auto;
}

.container {
  max-width: 120rem;
  width: 100%;
  margin: 0 auto;
}

.hidden {
  display: none;
}

@font-face {
  font-family: "Roboto-Bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto-Light";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #F6F8F9;
  box-shadow: 0 0 10px 5px rgba(116, 110, 78, 0.5);
  z-index: 1;
}

.nav {
  position: relative;
}
@media (max-width: 767px) {
  .nav {
    overflow: auto;
    height: 100vh;
  }
}
.nav__header {
  display: none;
  width: 100%;
  height: 7rem;
  padding: 5rem 2rem;
  background-color: #f0f0f0;
  box-shadow: 0 0 10px 5px rgba(116, 110, 78, 0.5);
}
@media (max-width: 767px) {
  .nav__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.nav__title {
  font-size: 4rem;
  text-transform: uppercase;
}
.nav__burger {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: transparent;
  outline: none;
  border: 0.2rem solid rgba(116, 110, 78, 0.5);
  border-radius: 0.5rem;
}
.nav__burger:hover {
  border: 0.2rem solid #373535;
}
.nav__burger:hover .nav__icon-burger {
  stroke: #373535;
  rotate: 180deg;
}
.nav__burger:hover .nav__icon-close {
  fill: #373535;
  rotate: 180deg;
}
.nav__icon-burger, .nav__icon-close {
  width: 5rem;
  height: 5rem;
  stroke: rgba(116, 110, 78, 0.5);
  stroke-width: 3;
  stroke-linecap: round;
}
.nav__icon-close {
  fill: rgba(116, 110, 78, 0.5);
}
.nav__icon-close._hidden, .nav__icon-burger._hidden {
  display: none;
}
@media (min-width: 767px) {
  .nav__menu {
    display: flex;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .nav__menu {
    display: none;
  }
}
.nav__menu._active {
  display: block;
}
.nav__item {
  margin: 0 auto;
}
.nav__dropdown {
  display: block;
  font-size: 2.4rem;
  color: #373535;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  padding: 2rem;
}
.nav__dropdown:hover {
  background-color: rgba(116, 110, 78, 0.5);
}
.nav__dropdown:focus {
  outline: none;
  background-color: rgba(116, 110, 78, 0.5);
}

.submenu {
  position: absolute;
  overflow-y: auto;
  overflow-x: hidden;
  top: 6.5rem;
  background-color: #fff;
  z-index: -10;
  padding: 0 3rem;
  left: 50%;
  transform: translate(-50%);
}
@media (max-width: 767px) {
  .submenu {
    position: relative;
  }
}
.submenu__title {
  font-size: 4rem;
}
.submenu__list--title {
  font-weight: 700;
}
.submenu__header {
  padding: 2rem 0;
  margin-bottom: 5rem;
  border-bottom: 0.1rem solid rgba(116, 110, 78, 0.5);
}
@media (max-width: 767px) {
  .submenu__header {
    display: none;
  }
}
.submenu__box {
  display: flex;
  gap: 3rem;
  width: 120rem;
}
@media (max-width: 767px) {
  .submenu__box {
    flex-direction: column;
    margin-top: 2rem;
  }
}
.submenu__list-item {
  font-size: 3rem;
  margin-bottom: 2rem;
}
.submenu__link {
  font-size: 2.4rem;
  font-weight: 700;
  text-decoration: none;
  color: #373535;
}
.submenu__link:hover {
  color: #a94442;
  text-decoration: underline;
}

#dropdown-coins {
  opacity: 0;
  visibility: hidden;
  transform: translate(-200%, -200%);
  pointer-events: none;
}
@media (max-width: 767px) {
  #dropdown-coins {
    display: none;
  }
}

#dropdown-coins._active {
  display: block;
}

body._pc .nav__coins:hover > #dropdown-coins {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%);
  pointer-events: all;
}

body._touch #dropdown-coins {
  opacity: 0;
  visibility: hidden;
  transform: translate(-200%);
  pointer-events: none;
}

body._touch #dropdown-coins._active {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%);
  pointer-events: all;
}